.movieList {
    padding-left: 0;
    display: flex;
    flex-flow: row wrap;
    justify-content: center;
}
.movieItem {
    width: 24%;
    margin-bottom: 1rem;
}
.movieInfo > * {
    font-size: 1rem;
    font-weight: 400;
}
.movieInfo h4 {
    margin-bottom: 0.5rem;
}
.movieInfo h4 span {
    font-size: 0.8rem;
    margin-left: 0.5rem;
    color: #999;
}
.movieInfo p {
    margin-top: 0;
    color: #ccc;
}
.movieWrap {
    width: 500px;
    margin: 0 auto 3rem;
    padding: 2rem;
}

.movie h2 {
    position: relative;
}

.downloadCount {
    position: absolute;
    bottom: 0;
    right: 0;
    font-size: 0.9rem;
    font-weight: 400;
    color: #999999;
}
